// actions types

export const GO_TO_HISTORY = 'GO_TO_HISTORY'
export const NEXT_STEP = 'NEXT_STEP'

// action creators

export const GoToHistory = step => ({
    type: GO_TO_HISTORY,
    payload: {
        step
    }
})

export const NextStep = step => ({
    type: NEXT_STEP,
    payload: {
        step
    }
})