import { GO_TO_HISTORY, NEXT_STEP } from '../actions/gameActions';
import { gameState, displayWinner } from '../app/gameState';

const rootReducer = (state = gameState, action) => {
    const newState = { ...state };
    switch (action.type) {
        case GO_TO_HISTORY:
            newState.stepNumber = action.payload.step;
            newState.xIsNext = (action.payload.step % 2) === 0;
            return newState;
        case NEXT_STEP:
            const history = newState.history.slice(0, newState.stepNumber + 1);
            const currentStep = history[history.length - 1];
            const squares = currentStep.squares.slice();
            if (displayWinner(squares) || squares[action.payload.step]) {
                return state;
            }
            squares[action.payload.step] = newState.xIsNext ? 'X' : 'O';
            newState.history = history.concat([{
                squares: squares
            }]);
            newState.stepNumber = history.length;
            newState.xIsNext = !newState.xIsNext;
            return newState;
        default:
            return state;
    }
};

export default rootReducer;