import React from 'react';
import Board from './Board';
import { displayWinner } from '../app/gameState';
import Paper from '@material-ui/core/Paper';
import Divider from '@material-ui/core/Divider';
import '../css/game.css';

class Game extends React.Component {
    render() {
        const history = this.props.history;
        const current = history[this.props.stepNumber];
        const winner = displayWinner(current.squares);

        const moves = history.map((step, move) => {
            const desc = move ?
                'Go to move #' + move :
                'Start a game';
            return (
                <li key={move} className="history-item" onClick={() => this.props.onGoToHistory(move)}>                    
                    <p><span className="badge badge-info mr-2">Move #{move}</span><span className="text-muted">{desc}</span></p>
                </li>
            );
        });

        let status;
        if (winner) {
            status = "Winner: " + winner;
        } else {
            status = "Next player: " + (this.props.xIsNext ? "X" : "O");
        }
        return (
            <div className="game game-flex-direction">
                <div className="game-board">
                    <Board
                        squares={current.squares}
                        onClick={i => this.props.onNextStep(i)} />
                </div>
                <div className="game-info margin-top">
                    <Paper className="p-3 history">
                        <h4 className="text-info"><i className="fa fa-history mr-3" aria-hidden="true"></i>Game History</h4>
                        <Divider />
                        <ul className="history-container mt-4">
                            {moves}
                        </ul>
                        <ol></ol>
                    </Paper>
                </div>
            </div>
        );
    }
}

export default Game;